.main-objects{
  .main__content{
    max-width: 61.25em;
    gap: 1.44em;
  }
  .main-search{
    .fg{
      input{
        border: 1px solid rgba(255, 255, 255, 0.24);
        background: rgba(255, 255, 255, 0.08);
        color: #fff;
        &::placeholder{
          color: #CACCCD;
        }
        &:focus{
          border-color: #fff;
        }
      }
    }
  }
}

.main-search{
  position: relative;
  &__btn{
    @include center;
    position: absolute;
    top: 50%;
    right: 1.25em;
    transform: translateY(-50%);
    width: 1em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  .fg{
    input{
      height: 3.8125em;
      padding-right: 3em;
    }
  }
}

.objects{
  padding: 3.75em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 22.1875em - 1.25em) 22.1875em;
    gap: 3.5em 1.25em;
    align-items: flex-start;
  }
  &__sidebar{
    @include grid100gap(1.875em);
  }
  &__items{
    row-gap: 1.875em;
  }
}

.chapter-menu{
  border: .4375em solid #F8F8F8;
  padding: 1.5em;
  &__bg,
  &__top{
    display: none;
  }
  &__links{
    @include grid100gap(1.875em);
  }
  &__link{
    display: flex;
    align-items: flex-start;
    font-weight: 500;
    span{
      font-size: 1.0625em;
      line-height: 153%;
    }
    svg{
      width: .3125em;
      height: auto;
      flex-shrink: 0;
      margin-right: .625em;
      margin-top: .5em;
    }
    &:hover{
      color: $primary;
      path{
        fill: $primary
      }
    }
  }
}

.chapter-menu-btn{
  display: none;
}

.subscribe{
  padding: 1.875em;
  background: #F8F8F8;
  &__top{
    @include grid100gap(.44em);
    text-align: center;
    .text17{
      color: #A8A8AA;
    }
  }
  &__wrap{
    @include grid100gap(.94em);
    margin-top: 1.25em;
  }
  .fg input,
  .m-btn{
    height: 3.6875em;
  }
  .error-text{
    position: static;
  }
  .m-check{
    margin-top: .94em;
    label{
      &:before{
        top: 0;
      }
      span{
        font-size: .75em;
        line-height: 167%;
      }
    }
  }
}

.news-preview{
  padding: calc(1.875em - 1px);
  border: 1px solid #F1F1F1;
  .line-title{
    margin-left: -1.875em;
    border-left-width: .1875em;
    padding-left: 1.6875em;
  }
  &__items{
    margin-top: 1.56em;
    @include grid100gap(1.19em);
  }
  .more-btn{
    margin-top: 1.19em;
    .m-btn{
      width: 100%;
    }
  }
}

.preview-item{
  @include grid100gap(.44em);
  .text16{
    line-height: 144%;
    color: #36393B;
  }
  &__date{
    display: flex;
    align-items: center;
    color: #A6A6A6;
    img{
      width: 1.25em;
      flex-shrink: 0;
      margin-right: .44em;
    }
    span{
      font-size: .875em;
      line-height: 193%;
    }
  }
}

.objects-top{
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #F1F1F1;
  margin-bottom: 2.19em;
}

.filter{
  display: flex;
  align-items: center;
  margin-bottom: -1px;
  &__btn{
    display: flex;
    align-items: center;
    height: 2.3125em;
    text-transform: uppercase;
    @include lastM(0 2em 0 0);
    border-bottom: 1px solid $primary;
    color: $primary;
    svg{
      width: .5em;
      height: auto;
      margin-right: .5em;
    }
    span{
      font-size: .75em;
    }
    &:not(._active){
      color: #363738;
      border-color: transparent;
      svg{
        width: 0;
        margin: 0;
      }
    }
    &._reverse{
      svg{
        transform: rotate(-180deg);
      }
    }
  }
}

.object-banner{
  margin: 1.875em 0;
  grid-column: 1/-1;
  padding: 2.5em 2.94em;
  color: #fff;
  position: relative;
  &:before{
    z-index: 2;
  }
  &__content{
    position: relative;
    z-index: 3;
    max-width: 33.75em;
  }
  &__img{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    margin-top: 1.5em;
    .m-btn{
      width: 12.8125em;
      margin-right: 2em;
      img{
        width: .6875em;
        margin-left: .625em;
      }
    }
  }
  &__count{
    opacity: .7;
  }
}


@media screen and (max-width: 992px){
  .main-objects{
    .main__content{
      max-width: 34.875em;
      gap: 1.25em;
    }
  }

  .main-search{
    .fg{
      input{
        font-size: .875em;
        height: 4.35em;
        padding: 0 3.25em 0 1.43em;
      }
    }
  }

  .objects{
    padding: 2.12em 0;
    &._hide-sidebar-mob{
      .objects__sidebar{
        display: none;
      }
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__items{
      row-gap: 1.56em;
    }
  }

  ._sidebar-mob-place{
    padding: 3.5em 0 2.12em;
  }

  .chapter-menu{
    border: none;
    padding: 0 0 1.875em;
    &._active{
      transform: translateX(0);
      &~.chapter-menu__bg{
        display: block;
      }
    }
    &__top{
      display: flex;
    }
    &__links{
      padding: 0 1.875em 1.875em;
    }
  }

  .chapter-menu-btn{
    margin-bottom: 1.56em;
    width: 100%;
    height: 2.9375em;
    @include center;
    border: 1px solid #EBEBEB;
    color: #363738;
    text-transform: uppercase;
    span{
      font-size: .75em;
      line-height: 120%;
    }
    svg{
      width: 1.5em;
      height: auto;
      margin-right: 1.0625em;
    }
    &:hover{
      background-color: $black;
      border-color: $black;
      color: #fff;
      path{
        stroke: #fff;
      }
    }
  }

  .subscribe{
    margin-top: 0;
    @media screen and (min-width: 577px){
      text-align: center;
      &__wrap{
        grid-template-columns: calc(100% - 13.5em - .94em) 13.5em;
      }
      .m-check{
        margin-left: auto;
        margin-right: auto;
        display: inline-flex;
      }
    }
  }

  .objects-top{
    margin-bottom: 1.875em;
  }

  .object-banner{
    margin: .94em 0;
  }
}


@media screen and (max-width: 576px) {
  .main-objects {
    .main__content {
      gap: .69em;
    }
  }

  .subscribe{
    padding: 1.875em .94em;
    &__top{
      padding: 0 .84em;
      .text17{
        font-size: 1em;
        line-height: 144%;
      }
    }
    &__title{
      font-size: 1.3125em;
      line-height: 133%;
      padding: 0 .5em;
    }
  }

  .objects-top{
    flex-direction: column;
    @include fullScreenMob;
    .filter{
      width: 100%;
      margin-top: 1.25em;
      &:first-child{
        margin-top: 0;
      }
    }
  }

  .object-banner{
    min-height: 29.4375em;
    display: flex;
    align-items: flex-end;
    padding: 1.56em .91em 1.56em 1.52em;
    &__nav{
      margin-top: 1.25em;
      .m-btn{
        margin: 0;
      }
    }
    &__count{
      display: none;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
