.main{
  background-size: cover;
  background-position: center;
  color: #fff;
  text-align: center;
  &__inner{
    position: relative;
    z-index: 2;
    min-height: 31.5625em;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2em 0;
  }
  &__content{
    max-width: 50em;
    @include grid100gap(1.19em);
  }
  &__desc{
    opacity: .8;
    line-height: 185%;
  }
  &__city{
    text-transform: uppercase;
    line-height: 61%;
    letter-spacing: 0.07875em;
    opacity: .6;
  }
  .breadcrumbs__item{
    color: #fff;
    opacity: 0.9;
    &:last-child{
      color: #fff;
    }
  }
}

.districts{
  padding: 4.44em 0;
  background-color: #F2F2F2;
  background-image: url(../img/figure.svg);
  background-size: auto 100%;
  background-position: right top;
  background-repeat: no-repeat;
  &__inner{
    display: grid;
    grid-template-columns: 49.8125em calc(100% - 49.8125em - 3.125em);
    justify-content: space-between;
  }
}

.district-info{
  padding: 6em 0;
  display: none;
  &._active{
    display: block;
  }
  &__top{
    @include grid100gap(.75em);
    .text16{
      color: #515151;
      line-height: 169%;
    }
  }
  &__links{
    margin-top: 1.875em;
    @include grid100gap(1.5625em);
  }
}

.district-link{
  padding: 1.12em 1.38em;
  border-radius: 0.125em;
  border: 1px solid rgba(255, 255, 255, 0.06);
  background: #FFF;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:hover{
    outline: 1px solid $primary !important;
    border-color: $primary;
    color: $black;
    .district-link__arrow{
      background-color: $black;
    }
  }
  &__title{
    font-size: 1.1875em;
    line-height: 131%;
    max-width: 21em;
    font-weight: 500;
  }
  &__arrow{
    transition: inherit;
    background-color: $primary;
    @include center;
    width: 3.1875em;
    height: 3.1875em;
    border-radius: 50%;
    margin-left: .5em;
    flex-shrink: 0;
    img{
      width: .6875em;
    }
  }
}

.last-news{
  padding: 3.75em 0 3.125em;
  &__wrap{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 61.25em calc(100% - 62.5em);
    gap: 1.25em;
    align-items: flex-start;
  }
  &__items{
    @include grid100gap(1.25em);
  }
  .news-items{
    margin-top: 1.25em;
  }
}

.line-title{
  padding-left: .9375em;
  border-left: .3125em solid $primary;
}

.big-news{
  padding: 2.5em;
  min-height: 28em;
  display: flex;
  align-items: flex-end;
  background-size: cover;
  background-position: center;
  color: #fff;
  &:before{
    background: linear-gradient(0deg, rgba(26, 26, 26, 0.64) 0%, rgba(26, 26, 26, 0.64) 100%);
  }
  &__content{
    max-width: 34.75em;
    position: relative;
    z-index: 2;
    @include grid100gap(1.44em);
  }
  &__nav{
    display: flex;
  }
  &__btn{
    height: 2.8125em;
    span{
      font-size: .9375em;
    }
    img{
      width: .6875em;
      margin-left: .625em;
    }
  }
  &__date{
    opacity: 0.8;
    line-height: 193%;
  }
  .text25{
    @include truncate(3);
  }
}

.news-item,
.news-card{
  &__img{
    display: block;
    overflow: hidden;
    @include imgCover(56%);
    img{
      transition: all .75s;
    }
    &:hover{
      img{
        transform: scale(1.05);
      }
    }
  }
}

.news-item{
  display: grid;
  grid-template-columns: 10.8125em calc(100% - 10.8125em - .94em);
  gap: .94em;
  align-items: flex-start;
  &__content{
    @include grid100gap(.56em);
  }
  .text14{
    color: #9EA6A9;
    line-height: 136%;
  }
}

.news-card{
  &__img{
    transition: all .75s;
    &:hover{
      transform: scale(1.025);
    }
    &-wrap{
      margin-bottom: .94em;
      overflow: hidden;
    }
  }
  &__bottom{
    margin-top: .5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .text14{
    color: #9EA6A9;
    line-height: 193%;
  }
}

.more-btn{
  display: flex;
  justify-content: center;
  margin-top: 3.75em;
  .m-btn{
    width: 18.4375em;
    max-width: 100%;
    img{
      width: .6875em;
      margin-left: .625em;
    }
  }
}

.photo-report{
  padding: 3.125em 0;
  .tab-content{
    margin-top: 2.19em;
  }
}

.report-card{
  display: block;
  position: relative;
  color: #fff;
  overflow: hidden;
  &:hover{
    color: #fff;
    .report-card__img{
      transform: scale(1.05);
    }
  }
  &__img{
    transition: all .75s;
    @include imgCover(76.25%);
    &:before{
      background: linear-gradient(0deg, rgba(26, 26, 26, 0.50) 0%, rgba(26, 26, 26, 0.50) 100%);
    }
  }
  &__content{
    z-index: 2;
    position: absolute;
    bottom: 0;
    left: 0;
    max-width: 21em;
    padding: 2.19em;
    @include grid100gap(.28em);
  }
  &__count{
    display: flex;
    align-items: center;
    font-weight: 500;
    opacity: 0.7;
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .44em;
    }
    .text14{
      line-height: 193%;
    }
  }
}



@media screen and (max-width: 992px){
  .main{
    background-size: auto 100%;
    &__content{
      gap: .69em;
      max-width: 30em;
    }
    &__city{
      line-height: 73%;
    }
    &__desc{
      line-height: 147%;
    }
  }

  .districts{
    padding: 2.19em 0 3.12em;
    background-position: left bottom;
    background-size: 100% auto;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
  }

  .district-info{
    padding: 0;
    &__top{
      text-align: center;
      .text16{
        line-height: 157%;
      }
    }
    &__links{
      gap: .94em;
      margin-top: 1.25em;
    }
  }

  .district-link{
    padding: .81em 1.25em;
    &__title{
      font-size: .9375em;
      max-width: unset;
    }
  }

  .last-news{
    padding: 2.5em 0 1.8em;
    &__wrap{
      margin-top: 1.875em;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    @media screen and (min-width: 577px){
      .last-news__items{
        display: none;
      }
    }
    .news-items{
      row-gap: 1.56em;
    }
  }

  .big-news{
    min-height: 20.6875em;
    padding: 2.31em 1.56em;
    &__content{
      max-width: 27em;
      gap: .94em;
    }
  }

  .news-card{
    .text19{
      font-size: 1em;
      line-height: 119%;
    }
  }

  .more-btn{
    margin-top: 1.56em;
  }

  .photo-report{
    padding: 1.8em 0;
    .tab-content,
    .more-btn{
      margin-top: 1.875em;
    }
  }

  .report-card{
    &__content{
      padding: 2.5em 1.56em;
    }
  }
}


@media screen and (max-width: 576px){
  .districts{
    padding: 2.37em 0 2.5em;
    &__inner{
      gap: 1em;
    }
  }

  .district-info{
    &__link{
      margin-top: 1.56em;
    }
  }

  .last-news{
    padding-bottom: 2.2em;
    &__wrap{
      margin-top: 2.31em;
    }
  }

  .big-news{
    padding: 1.25em;
    min-height: 22.875em;
    &__content{
      gap: 1.25em;
    }
  }

  .news-item{
    grid-template-columns: 8.125em calc(100% - 8.125em - .75em);
    gap: .75em;
    &__content{
      gap: .19em;
    }
    .text17{
      font-size: .875em;
    }
    .text14{
      font-size: .75em;
      line-height: 158%;
    }
  }

  .more-btn{
    margin-top: 3.44em;
  }

  .photo-report{
    padding: 2.2em 0;
    .more-btn,
    .tab-content{
      margin-top: 2.19em;
    }
  }

  .report-card{
    &__content{
      padding: 1.56em;
    }
  }
}
