.ingredients{
  margin-top: 2.1875em;
  &__items{
    margin-top: 1.875em;
    padding: 2.19em;
    background: #FAFAFA;
    @include grid100gap(.56em);
  }
}


.ingredient-item{
  display: flex;
  align-items: center;
  .text15{
    line-height: 187%;
  }
  &__desc{
    color: #686767;
    order: 2;
  }
  &:after{
    content: '';
    flex-grow: 1;
    height: 1px;
    min-width: .375em;
    border-top: 1px dashed #D1D1D1;
    margin: 0 .94em;
  }
}

.cooking{
  margin-top: 2.5em;
  &__items{
    margin-top: 2em;
    @include grid100gap(2.5em);
  }
}

.cook-step{
  &__img{
    width: 100%;
    display: block;
    margin-top: 1.25em;
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__num{
    width: 2.5em;
    height: 2.5em;
    flex-shrink: 0;
    @include center;
    margin-right: 1.06em;
    background: #F3F3F3;
    font-weight: 500;
    color: $primary;
    span{
      font-size: 1.3125em;
      line-height: 120%;
    }
  }
  &__desc{
    color: #3D3F40;
    padding-left: 3.56em;
    .text15{
      line-height: 173%;
    }
  }
}

.quote{
  margin-top: 2.5em;
  background: #FAFAFA;
  border-left: .3125em solid $primary;
  padding: 2.19em 1.875em;
  &__text{
    color: #3D3F40;
    line-height: 176%;
    font-weight: 500;
  }
}

.recipe-page{
  .photo-report{
    padding: 3.75em 0 0;
  }
  .article{
    margin-top: 1.875em;
  }
}


@media screen and (max-width: 992px){
  .ingredients{
    margin-top: 1.875em;
    &__items{
      padding: 1.56em;
    }
  }

  .cooking{
    margin-top: 1.875em;
    &__items{
      margin-top: 1.56em;
      gap: 1.875em;
    }
  }

  .cook-step{
    &__img{
      margin-top: .94em;
    }
    &__desc{
      padding: .94em 0 0;
    }
  }

  .recipe-page{
    .article{
      margin-top: 1.56em;
    }
  }
}


@media screen and (max-width: 576px){
  .ingredients{
    &__items{
      padding: .94em;
    }
  }

  .cook-step{
    &__top{
      align-items: flex-start;
    }
  }
}
