.report-page{
  padding: 3.75em 0;

  .article{
    margin-top: 1.56em;
    max-width: 69em;
    p{
      font-size: 1.0625em;
      line-height: 176%;
    }
  }

  @media screen and (min-width: 993px){
    .subscribe{
      display: grid;
      grid-template-columns: 30.25em calc(100% - 44.25em - 3.125em) 14em;
      margin-top: 5em;
      gap: 1.5625em;
      align-items: center;
      padding: 1.875em 3.125em;
      &__top{
        text-align: left;
      }
      &__wrap{
        margin: 0;
        grid-template-columns: calc(100% - 13.5em - .94em) 13.5em;
      }
      .error-text{
        position: absolute;
      }
    }
  }
}


.report-gallery{
  margin-top: 1.56em;
  position: relative;
  &__wrap{
    margin-top: 2.5em;
  }
  &__img{
    display: block;
    @include imgCover(51.4%);
  }
  &__big{
    position: relative;
  }
  &__small{
    margin-top: 1.625em;
    overflow: hidden;
    .swiper{
      width: calc(100% + 1.625em);
      &-slide{
        width: calc(100% / 3);
        padding-right: 1.625em;
        &-thumb-active{
          .report-gallery__img{
            border-color: $black;
          }
        }
      }
    }
    .report-gallery__img{
      transition: all .3s;
      border: 4px solid #E0E0E0;
    }
  }
  .swiper{
    &-button{
      top: 50%;
      margin: -7.5em 0 0 0;
      transform: translateY(-50%);
      &-prev{
        left: -5.6875em;
      }
      &-next{
        right: -5.6875em;
      }
    }
  }
}



@media screen and (max-width: 992px){
  .report-page{
    padding: 1.56em 0 0;
    .subscribe{
      display: none;
    }
    @media screen and (min-width: 577px){
      .subscribe{
        margin-top: 3.75em;
        &__wrap{
          margin-top: 1.25em;
          grid-template-columns: calc(100% - 13.5em - .94em) 13.5em;
        }
        .error-text{
          position: absolute;
          font-size: .6875em;
        }
      }
    }
  }

  .report-gallery{
    margin: 0;
    &__wrap{
      margin-top: 1.875em;
      .line-title{
        display: none;
      }
    }
    &__small{
      margin-top: 1.25em;
      .swiper{
        width: calc(100% + 1.25em);
        &-slide{
          padding-right: 1.25em;
        }
      }
    }
    .swiper{
      &-nav{
        @include center;
        margin-top: 1.875em;
      }
      &-button{
        position: static;
        margin: 0 .47em;
        transform: unset;
        flex-shrink: 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .report-page{
    .subscribe{
      margin-top: 3.75em;
    }
  }

  .report-gallery{
    &__small{
      display: none;
    }
  }
}
