.main2{
  padding: 3.125em 0;
  background: #F2F2F2;
  &__inner{
    text-align: center;
  }
  .breadcrumbs{
    margin-bottom: 1em;
  }
}

.article,
.article p{
  font-size: 1em;
  line-height: 181%;
}

.article{
  color: #3D3F40;
  img{
    width: 100%;
    display: block;
  }
  h2{
    font-weight: 500;
    margin-top: 1.08em;
    margin-bottom: .67em;
    padding-left: .54em;
    border-left: .13em solid $primary;
    &:first-child{
      margin-top: 0;
    }
  }
  h3{
    font-weight: 500;
    margin-bottom: .5em;
  }
  p{
    @include lastM(0 0 .5em);
  }
  hr{
    opacity: 0;
    height: 1.5em;
    display: block;
  }
  &__images{
    margin: 1.875em 0 1.25em;
    &:first-child{
      margin-top: 0;
    }
    &:last-child{
      margin-bottom: 0;
    }
  }
}

.object-item{
  .photo-report{
    padding: 0;
    margin-top: 3.75em;
  }
}

.collection{
  .slider{
    margin-top: 1.875em;
  }
  @media screen and (min-width: 993px){
    .line-title{
      padding-right: 4em;
      min-height: 3.1875em;
      display: flex;
      align-items: center;
    }
    .swiper{
      &-nav{
        display: flex;
        align-items: center;
        position: absolute;
        right: 0;
        bottom: 100%;
        margin-bottom: 1.875em;
      }
      &-button{
        position: static;
        transform: unset;
        margin: 0 0 0 .94em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main2{
    padding: 1.75em 0;
    .breadcrumbs{
      margin-bottom: 0;
    }
  }

  .article,
  .article p{
    line-height: 168%;
  }

  .article{
    h2{
      padding-left: .65em;
      font-size: 1.4375em;
      margin-top: 1.3em;
      margin-bottom: .78em;
    }
    &__images{
      margin-bottom: 1.94em;
    }
  }

  .collection2,
  .photo-report2{
    @media screen and (min-width: 577px){
      .swiper{
        &-container{
          width: calc(100% + .94em);
        }
        &-slide{
          width: calc(100% / 3);
          padding-right: .94em;
        }
      }
      .news-card{
        .text19{
          font-size: .9375em;
        }
        .text14{
          font-size: .8125em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .article{
    &__images{
      margin: 1.56em 0;
    }
  }

  .main2{
    padding: 0;
    &__inner{
      padding: 1em 0;
      min-height: 8.75em;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
}
