@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_slider.scss";
@import "components/_grid.scss";
._backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $fullHeight;
  background: rgba($black, 0.90);
  z-index: 19;
}

._cover{
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background: radial-gradient(44.75% 103.86% at 50% 50%, rgba(26, 26, 26, 0.61) 0%, rgba(26, 26, 26, 0.69) 100%);
  }
}

.tabs{
  margin-top: .875em;
  &:first-child{
    margin-top: 0;
  }
  &__title,
  &__select{
    display: none;
  }
  &__dropdown{
    display: flex;
    flex-wrap: wrap;
  }
}

.tab{
  margin: .6875em .6875em 0 0;
  padding: 0 1.625em;
  border: 1px solid #E7E7E7;
  height: 2.875em;
  color: #535353;
  @include center;
  transition: all .3s;
  span{
    font-size: .875em;
    line-height: 140%;
  }
  &._active{
    background: #F2F2F2;
    pointer-events: none;
  }
  &:hover{
    border-color: $primary;
    cursor: pointer;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.pagination{
  margin-top: 3.75em;
  &__items{
    display: flex;
    justify-content: center;
  }
  &__item{
    height: 3.5em;
    min-width: 3.5em;
    padding: 0 1.375em;
    border: 1px solid #F2F2F2;
    @include center;
    margin: 0 .3125em;
    font-weight: 400;
    color: #1B1B1B;
    span{
      font-size: .875em;
      line-height: 130%;
    }
    svg{
      width: 1.4375em;
      height: auto;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $black;
      border-color: $black;
      path{
        fill: #fff;
      }
    }
    &._btn{
      border: none;
      svg{
        &:first-child{
          margin-right: .625em;
        }
        &:last-child{
          margin-left: .625em;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .tabs{
    margin-top: 1.56em;
    &__dropdown{
      @media screen and (min-width: 577px){
        @include fullScreenTablet;
        overflow-x: auto;
        flex-wrap: nowrap;
        @include nullScroll;
      }
    }
  }

  .tab{
    flex-shrink: 0;
    margin-top: 0;
    height: 2.625em;
    padding: 0 1em;
    span{
      font-size: .8125em;
    }
  }

  .pagination{
    margin-top: 2.5em;
    ._hide-tablet{
      display: none;
    }
    &__item{
      &._btn{
        padding: 0;
        width: 3.75em;
        span{
          display: none;
        }
        svg{
          margin: 0 !important;
        }
        &:not(:hover){
          border: 1px solid #F2F2F2;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .tabs{
    position: relative;
    z-index: 3;
    &._show-dropdown{
      .tabs__dropdown{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__title{
      display: block;
      line-height: 180%;
    }
    &__select{
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 3.75em;
      border: 1px solid #D6D6D6;
      padding: 0 1.25em;
      color: #525252;
      margin-top: .3125em;
      svg{
        width: .5em;
        height: auto;
        flex-shrink: 0;
        margin-left: 1em;
      }
    }
    &__dropdown{
      pointer-events: none;
      opacity: 0;
      transition: all .3s;
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      background-color: #fff;
      border: 1px solid #D6D6D6;
      margin-top: .25em;
      z-index: 3;
      .tab{
        width: 100%;
        padding: 0 1.25em;
        justify-content: unset;
        border: none;
        &:not(:last-child){
          border-bottom: 1px solid #D6D6D6;
        }
      }
    }
  }
}
