.search{
  padding: 1.56em 0 3.125em;
  .main-search{
    &__btn{
      right: 1.56em;
    }
    .fg{
      input{
        height: 4.75em;
        padding-left: 1.56em;
      }
    }
  }
  &__links{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1.6em;
    width: calc(100% + .625em);
  }
  &__link{
    margin: .625em .625em 0 0;
    @include center;
    height: 2.75em;
    padding: 0 1.69em;
    border-radius: 0.125em;
    background: #F2F2F2;
    color: $black;
    &:hover,
    &._active{
      background: $primary;
      color: #fff;
    }
    &-title{
      font-size: .9375em;
      line-height: 110%;
    }
    &-num{
      opacity: 0.5;
      font-size: .875em;
      margin-left: .33em;
      line-height: 100%;
      &:before{
        content: '(';
      }
      &:after{
        content: ')';
      }
    }
  }
  &__items{
    margin-top: 3.69em;
    @include grid100gap(2.19em);
  }
}

.search-card2{
  @include grid100gap(.56em);
  .text37{
    span{
      background: #FFF8BC;
      display: inline-block;
      padding: 0 .1em;
    }
  }
  .text16{
    line-height: 181%;
    color: #3D3F40;
  }
  &__badge{
    display: inline-flex;
    align-items: center;
    margin-right: auto;
    padding: .25em 1.44em;
    min-height: 2.9375em;
    border-radius: 0.125em;
    background: #F9F9F9;
    color: #656565;
    span{
      font-size: 0.8125em;
      line-height: 120%;
    }
  }
}

.empty-search{
  margin-top: 6.88em;
  border: .4375em solid #F8F8F8;
  padding: 1.4375em;
  min-height: 7.3125em;
  font-weight: 500;
  @include center;
  img{
    width: 2.1875em;
    margin-right: .69em;
    flex-shrink: 0;
  }
  span{
    font-size: 1em;
    line-height: 150%;
  }
}


@media screen and (max-width: 992px){
  .search{
    padding-top: 3em;
    .main-search{
      .fg{
        input{
          height: 3.5em;
          padding-left: .94em;
        }
      }
      &__btn{
        right: .94em;
      }
    }
    &__links{
      margin-top: .9em;
    }
    &__link{
      height: 2.5em;
      padding: 0 .75em;
      &-title{
        font-size: .875em;
      }
      &-num{
        font-size: .75em;
        margin-left: .42em;
      }
    }
    &__items{
      margin-top: 1.56em;
      gap: 1.56em;
    }
  }

  .search-card2{
    &__badge{
      padding: .125em .81em;
      min-height: 2.6875em;
      span{
        font-size: .75em;
      }
    }
    .text16{
      font-size: .8125em;
      line-height: 169%;
    }
  }

  .empty-search{
    text-align: center;
    margin-top: 2.5em;
    flex-direction: column;
    img{
      margin: 0 0 .94em;
    }
  }
}


@media screen and (max-width: 576px){
  .search{
    padding-top: 1.56em;
  }
}
