.masters{
  &__items{
    row-gap: 2.19em;
  }
  .gallery,
  .videos,
  .photo-report{
    margin-top: 3.75em;
  }
  .photo-report{
    padding: 0;
  }
}

.master-card{
  &__img{
    display: block;
    @include imgCover(109.8%);
  }
  &__img{
    transition: all .75s;
    &:hover{
      transform: scale(1.025);
    }
    &-wrap{
      overflow: hidden;
    }
  }
  &__content{
    margin-top: .94em;
    text-align: center;
    @include grid100gap(.625em);
  }
  .text16{
    line-height: 168%;
    color: #3D3F40;
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
}

.master-item{
  display: grid;
  grid-template-columns: 22.1875em calc(100% - 22.1875em - 1.25em);
  gap: 1.25em;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  .article{
    p{
      line-height: 168%;
    }
  }
}


@media screen and (max-width: 992px){
  .masters{
    .gallery,
    .videos,
    .photo-report{
      margin-top: 1.875em;
    }
  }

  .master-item{
    grid-template-columns: 20.625em calc(100% - 20.625em - 1.25em);
  }
}


@media screen and (max-width: 576px){
  .masters{
    .gallery,
    .videos,
    .photo-report{
      margin-top: 2.5em;
    }
  }

  .master-item{
    grid-template-columns: 100%;
  }
}
