.groups{
  @media screen and (min-width: 993px){
    .objects{
      &__inner{
        grid-template-columns: 22.1875em calc(100% - 22.1875em - 1.25em);
      }
      &__sidebar{
        order: -1;
      }
    }
  }
  .collection2{
    @include lastM(0 0 3.75em);
  }
}

.collection2{
  &__top{
    padding-right: 8em;
    display: flex;
    align-items: center;
    .line-title{
      padding-right: 0;
    }
    .arrow-link2{
      margin-left: .25em;
    }
  }
  &._col4{
    margin: 1.875em 0;
    @media screen and (min-width: 993px){
      .swiper{
        &-slide{
          width: 25%;
        }
      }
    }
  }
}

.gallery{
  margin-top: 2.5em;
  &__grid{
    margin-top: 1.5625em;
  }
}

.gallery-img{
  &__wrap{
    display: block;
    @include imgCover(75.2%);
  }
}

.more-btn2{
  margin-top: 1.56em;
  display: flex;
  justify-content: center;
  .m-btn{
    height: 2.75em;
    color: $black;
    svg{
      width: 1.5em;
      height: auto;
      margin-right: .12em;
    }
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
}

.repertoire{
  margin-top: 2.5em;
  &__items{
    margin-top: 1.56em;
    line-height: 200%;
    p{
      line-height: 200%;
    }
  }
}

.videos{
  margin-top: 2.5em;
  &__grid{
    margin-top: 1.56em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5625em / 2));
    gap: 1.5625em;
    ._big{
      grid-column: 1/-1;
    }
  }
}

.film{

}


@media screen and (max-width: 992px){
  .collection2{
    &__top{
      padding-right: 0;
      justify-content: space-between;
    }
    &._col4{
      margin: 1.25em 0;
    }
  }

  .groups{
    .collection2{
      @include lastM(0 0 2.5em);
    }
  }

  .videos{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 1.31em / 2));
      gap: 1.31em;
    }
  }
}


@media screen and (max-width: 576px){
  .collection2{
    padding-bottom: 3.44em;
    position: relative;
    .arrow-link2{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  .gallery{
    margin-top: 1.56em;
  }

  .videos{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }
}
