.fg {
  position: relative;
  margin: 0;
  input{
    width: 100%;
    height: 3.375em;
    font-size: 1em;
    padding: 0 1.25em;
    border: 1px solid #F2F2F2;
    color: $black;
    &::placeholder{
      color: #757575;
    }
    &:focus {
      border-color: $black;
    }
  }
  &._error{
    input{
      border-color: $primary;
    }
    .error-text{
      display: block;
    }
  }
}

.error-text{
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  font-size: .8125em;
  line-height: 120%;
  margin-top: .25em;
  color: $primary;
}

.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    color: #8E8E8F;
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.875em;
    min-height: 1.125em;
    span{
      font-size: .875em;
      line-height: 164%;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0.09375em;
      transition: all .100s;
      background-color: #fff;
      border: 1px solid #D6D6D6;
      width: 1.25em;
      height: 1.25em;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}


.m-check {
  input {
    &:checked+label {
      &::before {
        background-image: url(../img/check.svg);
      }
    }
    &:hover+label {
      &::before {
        border-color: $primary;
      }
    }
  }
  label {
    &:before {
      border-radius: .1875em;
      @include bgImgSize(.625em);
    }
  }
  &._error{
    label{
      &:before{
        border-color: $primary;
      }
    }
    .error-text{
      margin-top: 0;
      display: block;
    }
  }
}
