.swiper-button{
  width: 3.1875em;
  height: 3.1875em;
  border: 1px solid #F2F2F2;
  @include center;
  svg{
    width: .5em;
    height: auto;
  }
  &:hover{
    border-color: $black;
  }
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-slide{
      padding-right: 1.25em;
    }
    &-button{
      top: 50%;
      margin: 0;
      transform: translateY(-50%);
      &-prev{
        left: -5.6875em;
      }
      &-next{
        right: -5.6875em;
      }
    }
  }
}

.report-slider{
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
  }
}


@media screen and (max-width: 992px){
  .slider{
    .swiper{
      &-nav{
        @include center;
        margin-top: 1.875em;
      }
      &-button{
        position: static;
        margin: 0 .47em;
        transform: unset;
        flex-shrink: 0;
      }
    }
  }

  .report-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .slider{
    .swiper{
      &-nav{
        margin-top: 2.19em;
      }
    }
  }

  .report-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }
}
