.footer{
  padding-top: 3.125em;
  &__logo{
    display: block;
    width: 13em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    max-width: 32.0625em;
    color: #72757B;
    &-item{
      font-size: .9375em;
      line-height: 140%;
      @include lastM(0 0 .75em);
    }
    &._hide-desktop{
      display: none;
    }
  }
  &__info{
    margin-right: 6.06em;
    flex-grow: 1;
    max-width: 54.5625em;
    display: grid;
    grid-template-columns: 16.4375em calc(100% - 16.4375em - 6.06em);
    justify-content: space-between;
    align-items: flex-start;
    gap: .75em;
  }
  &__links{
    @include grid100gap(2.19em);
  }
  &__conf,
  &__c{
    line-height: 128%;
    color: #676767;
  }
  &__conf{
    a{
      text-decoration: underline;
    }
  }
  &__by{
    line-height: 128%;
    a{
      text-decoration: underline;
    }
  }
}


.footer-top,
.footer-bottom{
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}


.footer-top{
  padding: .81em 0;
  border-top: 1px solid #E4E4E4;
  border-bottom: 1px solid #E4E4E4;
}

.footer-nav{
  display: flex;
  align-items: center;
  &__link{
    font-weight: 500;
    color: #303132;
    @include lastM(0 4.06em 0 0);
  }
}

.footer-bottom{
  padding: 3.125em 0 3.625em;
  &__inner{
    align-items: flex-start;
  }
}

.footer-subscribe{
  width: 31.875em;
  max-width: 100%;
  &__top{
    .text16{
      color: #AEAEB4;
      line-height: 144%;
    }
  }
  &__title{
    font-size: 1.3125em;
    line-height: 133%;
    font-weight: 500;
  }
  &__wrap{
    display: grid;
    grid-template-columns: calc(100% - 12.6875em) 12.6875em;
    margin-top: .625em;
    .m-btn{
      height: 3.375em;
    }
  }
  .m-check{
    margin-top: .375em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-top: 2.5em;
    &__desc{
      &-item{
        font-size: .8125em;
      }
    }
    &__info{
      margin: 0;
      max-width: unset;
      grid-template-columns: 100%;
      gap: .73em;
      .footer__desc{
        order: -1;
      }
    }
    &__links{
      gap: .73em;
    }
  }

  .footer-nav{
    &__link{
      @include lastM(0 1.44em 0 0);
      span{
        font-size: .8125em;
        line-height: 207%;
      }
    }
  }

  .footer-bottom{
    padding: 1.56em 0;
    &__inner{
      display: grid;
      grid-template-columns: calc(100% - 20.6875em - 1.5625em) 20.6875em;
    }
  }

  .footer-subscribe{
    &__top{
      .text16{
        font-size: .875em;
      }
    }
    &__wrap{
      grid-template-columns: 100%;
      gap: .56em;
    }
    .m-check{
      margin-top: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__desc{
      margin-top: .625em;
      padding-bottom: .81em;
      border-bottom: 1px solid #E4E4E4;
      @include fullScreenMob;
      text-align: center;
      &._hide-desktop{
        display: block;
      }
      &._hide-mob{
        display: none;
      }
    }
    &__links{
      gap: 1em;
      text-align: center;
    }
    &__by{
      font-size: .8125em;
    }
  }

  .footer-top{
    border-bottom: none;
    &__inner{
      flex-direction: column;
    }
  }

  .footer-nav{
    margin-top: .94em;
    //display: grid;
    //grid-template-columns: repeat(2,calc(50% - 1em));
    //gap: .75em 2em;
    display: block;
    width: 90%;
    margin-right: auto;
    column-count: 2;
    column-gap: 2em;
    &__link{
      @include lastM(0 0 .75em);
    }
  }

  .footer-bottom{
    padding: 1.56em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.625em;
    }
  }

  .footer-subscribe{
    order: -1;
    &__top{
      text-align: center;
    }
  }
}
