.header{
  position: relative;
  z-index: 10;
  border-bottom: 1px solid #F2F2F2;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__logo{
    display: block;
    margin-right: auto;
    width: 13.14em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-search{
  flex-shrink: 0;
  position: relative;
  &._show-field{
    .header-search__field{
      width: 54.3125em;
    }
  }
  &._show-dropdown{
    .header-search__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__toggle{
    position: relative;
    z-index: 5;
    width: 4.625em;
    height: 4.625em;
    padding: 0;
    @include center;
    svg{
      width: 1.125em;
      height: auto;
    }
    &:hover{
      path{
        stroke: #fff;
      }
    }
  }
  &__dropdown{
    max-width: 100vw;
    position: absolute;
    top: 100%;
    right: 0;
    border: 1px solid #F2F2F2;
    background: #FFF;
    width: 54.3125em;
    opacity: 0;
    pointer-events: none;
  }
  &__items{
    overflow-y: auto;
    max-height: 21.5em;
    @include scroll;
  }
  &__field{
    max-width: 100vw;
    overflow: hidden;
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    z-index: 4;
    width: 0;
    transition: all .3s;
    input{
      width: 100%;
      height: 100%;
      font-size: 1em;
      padding: 0 6em 0 1.25em;
      border: 1px solid #F2F2F2;
      &::placeholder{
        color: #757575;
      }
    }
  }
  &__link{
    @include center;
    height: 3.1875em;
    font-weight: 500;
    span{
      font-size: 1.1875em;
      line-height: 142%;
    }
  }
}

.search-card{
  padding: .94em 1.56em;
  @include grid100gap(.31em);
  &:nth-child(even){
    background: #F8F8F8;
  }
  .text13{
    color: #656565;
    line-height: 154%;
  }
  &__nav{
    display: flex;
  }
}

.menu{
  margin-right: 11em;
  &__bg{
    display: none;
  }
  &__top{
    display: none;
  }
}

.footer-nav,
.nav{
  &__show,
  &__link{
    display: flex;
    align-items: center;
    transition: all .3s;
    &:hover{
      color: $primary;
      path{
        fill: $primary;
        opacity: 1;
      }
    }
    span{
      font-size: 1em;
      line-height: 168%;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__item,
  &__link{
    @include lastM(0 3.75em 0 0);
  }
  &__show{
    cursor: pointer;
    svg{
      width: .5em;
      height: auto;
      margin-left: .375em;
    }
  }
  &__item{
    position: relative;
    &._active{
      .nav__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  @media screen and (min-width: 993px){
    &__hidden{
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      position: absolute;
      top: 99%;
      left: 0;
      width: 19.625em;
      padding-top: 1.4375em;
      &-inner{
        background: #FAFAFA;
        border-top: 2px solid $primary;
      }
    }
    &__sublink{
      padding: .94em;
      display: flex;
      align-items: center;
      &:hover{
        color: #fff;
        background-color: $primary;
      }
      span{
        font-size: .9375em;
        line-height: 140%;
      }
    }
  }

}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    .container{
      padding: 0;
    }
    &__inner{
      justify-content: space-between;
    }
    &__logo{
      margin: 0;
      width: 11.5625em;
    }
    &._open{
      .menu{
        transform: translateX(0);
        &__bg{
          display: block;
        }
      }
    }
  }


  .chapter-menu,
  .menu{
    position: fixed;
    width: 21.5625em;
    max-width: 100%;
    height: $fullHeight;
    background: #FFF;
    left: 0;
    top: 0;
    z-index: 20;
    overflow-y: auto;
    padding-bottom: 2em;
    transform: translateX(-150%);
    transition: all .3s;
    &__top{
      display: flex;
      justify-content: flex-end;
    }
    &__close{
      @include center;
      width: 4.375em;
      height: 4.375em;
      &:hover{
        path{
          opacity: 1;
        }
      }
    }
  }

  .burger{
    @include center;
    img{
      width: 1.125em;
    }
  }

  .burger,
  .header-search__toggle{
    width: 3.75em;
    height: 3.75em;
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link,
    &__item{
      @include lastM(0 0 1.38em);
    }
    &__show,
    &__link{
      padding: 0 1.875em;
      font-weight: 500;
    }
    &__item{
      width: 100%;
      &._active{
        .nav__show{
          color: $primary;
          svg{
            transform: rotate(-180deg);
          }
        }
      }
    }
    &__show{
      justify-content: space-between;
    }
    &__hidden{
      display: none;
      margin-top: 1em;
      background: #F9F9F9;
      padding: .94em .94em .94em 2.81em;
      &-inner{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
      }
    }
    &__sublink{
      @include lastM(0 0 .94em);
      span{
        font-size: .9375em;
        line-height: 150%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header-search__items{
    max-height: 25.5em;
  }
}
