.grid-column2,
.grid-column3,
.grid-column4{
  display: grid;
  gap: 1.25em;
}

.grid-column2{
  grid-template-columns: repeat(2,calc(50% - .625em));
}

.grid-column3{
  grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
}

.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
}


@media screen and (max-width: 992px) {
  .grid-column3,
  .grid-column4 {
    grid-template-columns: repeat(2, calc(50% - .625em));
  }

  .grid-column3-tablet{
    @media screen and (min-width: 577px){
      grid-template-columns: repeat(3,calc(100% / 3 - 1.875em / 3));
      gap: .9375em;

      .news-card{
        .text19{
          font-size: .9375em;
        }
        .text14{
          font-size: .8125em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px) {
  .grid-column2,
  .grid-column3,
  .grid-column4 {
    grid-template-columns: 100%;
  }
}
