.conf{
  padding: 2.44em 0;
  .article{
    p{
      font-size: .9375em;
      line-height: 180%;
    }
    a{
      font-weight: 700;
      text-decoration: underline;
      color: $primary;
    }
  }
}

.conf-hint{
  margin-bottom: 1.875em;
  border-radius: 0.25em;
  border: .3125em solid #F9F9F9;
  padding: .875em 1.25em;
}


@media screen and (max-width: 992px){
  .conf{
    padding: 1.56em 0;
  }
}


@media screen and (max-width: 576px){
  .conf{
    .article{
      p{
        font-size: .875em;
        line-height: 171%;
      }
    }
  }

  .conf-hint{
    margin-bottom: 1.56em;
    padding: .625em;
  }
}
