.calendar{
  padding: 3.75em 0;
  &__inner{
    display: grid;
    grid-template-columns: 28.8125em calc(100% - 28.8125em - 2.44em);
    justify-content: space-between;
  }
  .article{
    h2{
      padding-left: 0;
      border: none;
    }
  }
  .article2{
    margin-top: 2.5em;
  }
  .videos__grid{
    margin-top: 2.5em;
    @media screen and (min-width: 993px){
      .film._big{
        height: 34.4375em;
      }
      .film._small{
        height: 16.8125em;
      }
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
}

.calendar-item{
  border-radius: 0.375em;
  background: #F8F8F8;
  padding: 1.56em;
  input{
    display: none;
  }
  &__bottom{
    margin-top: 1.25em;
    padding: .94em;
    background-color: #fff;
    display: flex;
    align-items: flex-start;
    color: #303436;
    span{
      font-size: 1em;
      line-height: 144%;
    }
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
}

.air-datepicker-nav{
  border: none;
  padding: 0;
  &--action{
    width: 2.5625em;
    height: 2.5625em;
    background-color: #FFF;
    @include bgImgSize(.5em);
    border-radius: 50%;
    svg{
      display: none;
    }
    &[data-action="prev"]{
      background-image: url(../img/prev.svg);
    }
    &[data-action="next"]{
      background-image: url(../img/next.svg);
    }
    &:hover{
      background-color: #000;
      @include bgImgSize(.5em);
      &[data-action="prev"]{
        background-image: url(../img/prev2.svg);
      }
      &[data-action="next"]{
        background-image: url(../img/next2.svg);
      }
    }
  }
  &--title{
    font-size: 1.3125em;
    line-height: 128%;
    font-weight: 500;
  }
}

.air-datepicker{
  font-size: 1em;
  font-family: $font1;
  width: 100%;
  border: none;
  background-color: transparent;
  &--content{
    margin-top: 1.25em;
    border-radius: 0.4375em;
    border: 1px solid #E3E3E3;
    background: #FFF;
    padding: 0;
    overflow: hidden;
  }
  &-body{
    &--day-name{
      font-size: 0.9375em;
      color: #8B8B8B;
      font-weight: 500;
      text-transform: unset;
    }
    &--day-names{
      margin: 0;
      padding: .31em 1em;
      height: 3em;
      border-bottom: 1px solid #EAEAEA;
      background: #F8F8F8;
    }
    &--cells{
      padding: 1.25em 1.28em;
      grid-auto-rows: unset !important;
      gap: 0   .5em;
    }
  }
  &-cell{
    height: 2.8651em;
    font-size: 1em;
    border-radius: 50%;
    color: #464646;
    &.-selected-{
      background: #F5F5F5;
      color: #464646;
    }
    &.-current-{
      background: $primary;
      font-weight: 700;
    }
    &.-other-month-{
      opacity: 0;
    }
  }
}

.film2{
  .film{
    &__info{
      z-index: 2;
      flex-direction: column;
      @include centerAbsolute;
      .play-btn{
        margin: 0 0 .25em;
      }
      .text14{
        opacity: 0.7;
        line-height: 193%;
      }
    }
  }
}

.empty-day{
  border: .4375em solid #F8F8F8;
  padding: 1.4375em;
  @include grid100gap(.69em);
  .text16{
    color: #3D3F41;
    line-height: 168%;
  }
}

.events{
  margin-top: 1.875em;
  @include grid100gap(2.5em);
}

.event-card{
  &__top{
    display: flex;
    align-items: center;
    &:after{
      content: '';
      flex-grow: 1;
      min-width: 1px;
      height: 1px;
      border-top: 1px dashed #CCC;
      margin-left: .94em;
    }
  }
  &__day{
    display: flex;
    align-items: center;
    font-weight: 500;
    img{
      width: 1.6875em;
      flex-shrink: 0;
      margin-right: .56em;
    }
  }
  &__img{
    margin-top: 1.875em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    margin-top: .94em;
    @include grid100gap(.5em);
    .text16{
      line-height: 168%;
      color: #3D3F40;
    }
  }
}


@media screen and (max-width: 992px){
  .calendar{
    padding: 2em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__nav{
      margin-top: .94em;
    }
  }

  .calendar-item{
    padding: .9375em;
    @media screen and (min-width: 577px){
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      align-items: flex-start;
      &__bottom{
        margin-top: 3.8125em;
      }
    }
  }

  .air-datepicker{
    &-body{
      &--cells{
        padding: 1.25em;
        gap: 0 .1875em;
      }
    }
    &-cell{
      height: 2.45em;
    }
  }

  .empty-day{
    .text25{
      font-size: 1.1875em;
      line-height: 126%;
    }
    .text16{
      font-size: .9375em;
      line-height: 173%;
    }
  }
}


@media screen and (max-width: 576px){
  .calendar-item{
    @include fullScreenMob;
    &__bottom{
      padding: .9375em .625em;
      img{
        margin-right: .5em;
      }
      span{
        font-size: .75em;
        line-height: 150%;
      }
    }
  }

  .event-card{
    &__content{
      .text19{
        font-size: 1em;
      }
      .text16{
        font-size: .9375em;
        line-height: 160%;
      }
    }
  }
}
