
.map{
  position: relative;
  &__inner{
    //padding-bottom: 95.1%;
    //position: relative;
  }
  &__borders{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: auto;
    display: none;
    //opacity: 0;
    //display: none;
  }
  &__img{
    width: 100%;
    height: auto;
    display: block;
  }
  g._active,
  g:hover{
    cursor: pointer;
    filter: drop-shadow(0 0 41px rgba(0,0,0,.4));
    z-index: 10;
    position: relative;
    ._stroke {
      stroke: #fff;
      z-index: 10;
      position: relative;
    }
  }
}

.map-group{
  //pointer-events: none;
  position: absolute;
  //z-index: -1;
  //display: none;
  display: none;
  svg,
  img{
    width: 100%;
    height: auto;
  }
  svg{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
  }
  &._active{
    z-index: 10;
    filter: drop-shadow(0px 0px 41px rgba(0, 0, 0, 0.40));
    path{
      stroke: #FFF;
    }
    svg{
      z-index: 3;
    }
  }
}

