.kdu-items{
  margin-top: 1.875em;
  @media screen and (min-width: 993px){
    gap: 1.31em;
    grid-template-columns: repeat(2,calc(50% - 1.31em / 2));
    .news-card__img{
      margin-bottom: 1em;
    }
  }
}

.films{
  padding: 3.125em 0 4.37em;
  background: #FBFBFB;
  margin-top: 1.5em;
  &__grid{
    margin-top: 3em;
    display: grid;
    grid-template-columns: 53.4375em calc(100% - 53.4375em - 1.25em);
    gap: 1.25em;
    align-items: flex-start;
  }
  &__items{
    height: 30.0625em;
    overflow-y: auto;
    padding-right: 1.56em;
    @include scroll;
    @include grid100gap(1.25em);
  }
}

.film{
  height: 30.0625em;
  max-height: 100%;
  position: relative;
  color: #fff;
  &._big{
    @media screen and (min-width: 577px){
      height: 38.8125em;
    }
  }
  &._small{
    @media screen and (min-width: 577px){
      height: 18.96em;
      .film{
        &__content{
          max-width: 20em;
          padding: 1.875em;
          .text23{
            font-size: 1.125em;
            line-height: 150%;
          }
        }
      }
    }
  }
  &__img{
    @include imgCover(56.2%);
    height: 100%;
    padding-bottom: 0;
    &:before{
      background: linear-gradient(0deg, rgba(26, 26, 26, 0.50) 0%, rgba(26, 26, 26, 0.50) 100%);
    }
  }
  &__content{
    z-index: 2;
    position: absolute;
    bottom: 0;
    left: 0;
    max-width: 28em;
    width: 100%;
    padding: 3.125em;
    @include grid100gap(1.56em);
  }
  &__type{
    text-transform: uppercase;
    line-height: 84%;
    opacity: .6;
    letter-spacing: 0.05688em;
  }
  &__info{
    display: flex;
    align-items: center;
    &-text{
      margin-left: .94em;
      @include grid100gap(.25em);
    }
  }
}

.play-btn{
  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  padding: 0;
  flex-shrink: 0;
  img{
    width: .93em;
  }
  &._big{
    width: 4.0625em;
    height: 4.0625em;
    img{
      width: 1.5em;
    }
  }
}

.film-card{
  display: grid;
  grid-template-columns: 16.375em calc(100% - 16.375em - 1.125em);
  gap: 1.125em;
  align-items: center;
  &__img{
    @include imgCover(56.1%);
  }
  .play-btn{
    @include centerAbsolute;
    z-index: 2;
  }
  &__content{
    @include grid100gap(.31em);
  }
  .text14{
    color: #A6A6A6;
    line-height: 193%;
  }
}

.photo-report3{
  padding: 3.38em 0;
  background: #FBFBFB;
  &._white-bg{
    background-color: transparent;
  }
  &:last-child{
    margin-bottom: -3.38em;
  }
}

.kdu-item{
  .article{
    margin-top: 1.56em;
  }
}

.contacts{
  margin-top: 2.5em;
  &__grid{
    display: grid;
    grid-template-columns: 23.1875em calc(100% - 23.1875em - 2.38em);
    justify-content: space-between;
    margin-top: 1.56em;
    align-items: flex-start;
  }
  &__content{
    @include grid100gap(2.5em);
  }
}

.contact-item{
  @include grid100gap(.75em);
  &__link{
    color: $primary;
    text-decoration: underline;
  }
  &__hint{
    color: #444;
    margin-top: .25em;
  }
}

.map2{
  height: 35.875em;
}

.photo-report4{
  padding: 0;
  margin-top: 3.75em;
  @media screen and (min-width: 577px){
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }
}

.kdu-item{
  &__img{
    display: block;
    width: 100%;
    margin-bottom: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .kdu-items{
    .news-card__img{
      margin-bottom: .625em;
    }
  }

  .films{
    margin-top: 1.5em;
    padding: 2.5em 0;
    &__grid{
      margin-top: 1.56em;
      grid-template-columns: 23em calc(100% - 24.5em);
      position: relative;
      &:after{
        content: '';
        position: absolute;
        right: 0;
        bottom: -.91em;
        background: linear-gradient(180deg, rgba(251, 251, 251, 0.00) 0%, #FBFBFB 100%);
        height: 3.25em;
        width: calc(100% - 24.5em);
        z-index: 3;
      }
    }
    &__items{
      padding-right: .5em;
      gap: .94em;
      height: 15.75em;
    }
  }

  .film{
    height: 15.75em;
    &._big{
      @media screen and (min-width: 577px){
        height: 25.1875em;
      }
    }
    &._small{
      @media screen and (min-width: 577px){
        height: 12.25em;
        .film{
          &__content{
            .text23{
              font-size: 1.0625em;
              line-height: 135%;
            }
          }
        }
      }
    }
    &__content{
      padding: 1.25em;
      max-width: 19em;
      gap: .94em;
    }
    &__info{
      &-text{
        gap: .125em;
      }
    }
    &__type{
      font-size: .6875em;
    }
  }

  .play-btn{
    width: 2em;
    height: 2em;
    img{
      width: .74em;
    }
    &._big{
      width: 2.125em;
      height: 2.125em;
      img{
        width: .79em;
      }
    }
  }

  .film-card{
    grid-template-columns: 7.5em calc(100% - 8.125em);
    gap: .625em;
    align-items: flex-start;
    .text19{
      font-size: .875em;
      line-height: 128%;
    }
    .text14{
      font-size: .8125em;
      line-height: 138%;
    }
  }

  .photo-report3{
    padding: 2.5em 0;
    &:last-child{
      margin-bottom: -2.5em;
    }
  }

  .contacts{
    margin-top: 1.875em;
    &__grid{
      grid-template-columns: 16em calc(100% - 16em - 2.31em);
    }
    &__content{
      gap: .625em;
    }
  }

  .contact-item{
    &._phones{
      gap: 1.31em;
      .contact-item__desc{
        color: #525252;
      }
      a{
        color: $black;
        display: block;
        line-height: inherit;
      }
      span{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .kdu-items{
    margin-top: 1.56em;
  }

  .films{
    margin-top: 1.31em;
    padding-bottom: 2.97em;
    &__grid{
      grid-template-columns: 100%;
      &:after{
        bottom: -.59em;
        width: 100%;
      }
    }
    &__items{
      height: auto;
      max-height: 16.9375em;
    }
  }

  .film{
    height: 11.625em;
  }

  .contacts{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
    &__content{
      gap: 1.56em;
    }
  }
}
