@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 .94em;
    height: 3.6875em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
}

.arrow-link{
    display: flex;
    align-items: center;
    font-weight: 500;
    color: $primary;
    &:hover{
        span{
            text-decoration-line: underline;
        }
    }
    svg{
        width: .3125em;
        height: auto;
        margin-left: .375em;
    }
    span{
        font-size: 0.875em;
        line-height: 193%;
        transition: inherit;
    }
}

.m-btn-gray{
    background: #F2F2F2;
    color: $black;
    &:hover{
        background: $primary;
        color: #fff;
    }
}

.m-btn-red{
    background: $primary;
    color: #fff;
    &:hover{
        background: $black;
        color: #fff;
    }
}

.m-btn-black{
    background: $black;
    color: #fff;
    &:hover{
        background: $primary;
        color: #fff;
    }
}

.arrow-link2{
    white-space: nowrap;
    height: 3.1875em;
    display: flex;
    align-items: center;
    padding: 0 1.5em;
    color: $primary;
    font-weight: 500;
    &:hover{
        color: $black;
        path{
            fill: $black;
        }
    }
    svg{
        width: .625em;
        height: auto;
        margin-left: .625em;
    }
    span{
        font-size: .9375em;
        line-height: 160%;
        text-decoration: underline;
    }
}


@media screen and (max-width: 992px){
    .arrow-link2{
        height: 2.5em;
    }
}
