@import "../vars.scss";
.breadcrumbs {
    &__item {
        position: relative;
        display: inline;
        color: #464646;
        span{
            font-size: .875em;
            line-height: 193%;
        }
        &:last-child {
            color: #909090;
        }
        &:not(:first-child) {
            &:before {
                content: '';
                margin: 0 1em;
                font-size: .875em;
                line-height: 193%;
            }
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        ._hide-tablet{
            display: none;
        }
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        display: none;
    }
}
