.news{
  @media screen and (min-width: 993px){
    .big-news{
      &:not(._small){
        grid-column: 1/3;
      }
      &._small{
        .text25{
          font-size: 1.1875em;
        }
      }
    }
  }
}

.news-page{
  &__top{
    @include grid100gap(1.0625em);
  }
  &__date{
    color: #9EA6A9;
    line-height: 193%;
  }
  .article{
    margin-top: 1.875em;
    img{
      &:first-child{
        width: 29em;
        float: left;
        margin: 0 1.25em 0 0;
      }
    }
    i{
      font-style: italic;
      line-height: 176%;
    }
  }
}



@media screen and (max-width: 992px){
  .news{
    .big-news{
      grid-column: 1/-1;
      @media screen and (min-width: 577px){
        &._small{
          display: none;
        }
      }
    }
  }

  .news-page{
    padding-top: 1.56em;
    &__top{
      gap: .44em;
    }
    .chapter-menu-btn{
      margin: 1.12em 0 1.44em;
    }
    .article {
      margin-top: .94em;
      img {
        &:first-child {
          width: 100%;
          margin: 0 0 1.25em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .news-page{
    &__top{
      gap: .38em;
    }
    .chapter-menu-btn{
      margin: 0 0 1.18em;
    }
    .article{
      margin-top: 1.25em;
    }
  }
}
